@font-face {
  font-family: "Oswald-Medium";
  src: url("../fonts/Oswald-Medium.ttf"), url("../fonts/Oswald-Medium.woff"), url("../fonts/Oswald-Medium.woff");
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Oswald-Regular";
  src: url("../fonts/Oswald-Regular.ttf"), url("../fonts/Oswald-Regular.woff"), url("../fonts/Oswald-Regular.woff2");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "PlayfairDisplay-Regular";
  src: url("../fonts/PlayfairDisplay-Regular.ttf"), url("../fonts/PlayfairDisplay-Regular.woff"), url("../fonts/PlayfairDisplay-Regular.woff2");
  font-weight: 400;
  font-display: swap;
}
.header {
  margin: 45px 0 10px;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 24px;
}
.main__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  border: 3px solid #1E4D51;
  border-radius: 20px;
  padding: 0 32px;
  padding-bottom: 30%;
}
.main__img {
  width: 238px;
  height: 238px;
}
.main h2 {
  font-family: "Oswald-Medium", sans-serif;
  font-size: 28px;
  line-height: 94.5%;
  font-weight: 500;
  text-align: center;
  margin: 26px 0;
}
.main h1 {
  font-family: "Oswald-Regular", sans-serif;
  font-size: 28px;
  line-height: 94.5%;
  font-weight: 400;
  text-align: center;
  margin: 26px 0;
}
.main__date {
  font-family: "Oswald-Regular", sans-serif;
  font-size: 22px;
  line-height: 94.5%;
  font-weight: 400;
  text-align: center;
  margin-bottom: 10px;
}
.main__time {
  font-size: 16px;
  line-height: 94.5%;
  font-weight: 400;
  text-align: center;
}
.main__btn {
  width: 200px;
  height: 40px;
  margin-top: 26px;
  filter: drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.25));
  background: #1E4D51;
  box-shadow: 0px 4px 4px rgba(30, 77, 81, 0.25);
  border-radius: 5px;
  font-size: 16px;
  line-height: 94.5%;
  font-weight: 400;
  text-align: center;
  font-family: "PlayfairDisplay-Regular", sans-serif;
  color: #E4B0A2;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about__img {
  display: block;
  margin: 0 auto;
  padding: 32px 0 24px;
}
.about__text {
  padding: 16px 24px;
  margin: 24px;
  border: 1px solid #e4b0a2;
  font-size: 22px;
  line-height: 111.5%;
  font-weight: 500;
  letter-spacing: 0.045em;
  color: #e4b0a2;
}
.about__wrapper {
  background: #1e4d51;
}
.about__wrap--green {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about__btn__wrap {
  display: flex;
  justify-content: center;
  padding: 11px 0 30px;
}
.about__btn {
  color: #1e4d51;
  background-color: #e4b0a2;
  width: 146px;
  height: 40px;
  left: 24px;
  top: 6px;
  font-size: 22px;
  line-height: 111.5%;
  box-shadow: 4px 4px 4px rgba(228, 176, 162, 0.25);
}

.contacts__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contacts__wrap {
  display: flex;
  align-items: center;
  margin-bottom: 62px;
}
.contacts__name {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 22px;
  line-height: 100%;
  font-weight: 400;
  margin-bottom: 6px;
}
.contacts__title {
  font-size: 22px;
  line-height: 94.5% 400;
  font-family: PlayfairDisplay-Regular;
  text-align: center;
  padding-top: 32px;
  margin-bottom: 50px;
}
.contacts__title::before, .contacts__title::after {
  content: "";
  display: inline-flex;
  position: relative;
  top: -1px;
  width: 50%;
  height: 1px;
  vertical-align: middle;
  background: #1e4d51;
}
.contacts__title::before {
  left: -5px;
  margin-left: -50%;
}
.contacts__title::after {
  left: 5px;
  margin-right: -50%;
}
.contacts__telephone {
  font-family: Playfair Display;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
}
.contacts__tel {
  font-size: 20px;
  line-height: 100%;
  font-weight: 700;
  font-feature-settings: "pnum" on, "lnum" on;
}
.contacts__text {
  font-size: 22px;
  line-height: 100%;
  font-weight: 500;
  text-align: center;
  max-width: 294px;
  letter-spacing: 0.07em;
  margin-bottom: 62px;
}

.program h2 {
  font-family: "PlayfairDisplay-Regular", sans-serif;
  font-size: 24px;
  line-height: 94.5%;
  text-transform: uppercase;
  font-weight: 400;
  text-align: center;
  margin: 32px 0 20px;
  position: relative;
}
.program h2::after, .program h2::before {
  content: "";
  display: block;
  width: 24%;
  height: 1px;
  border: 1px solid #1E4D51;
}
.program h2::after {
  position: absolute;
  top: 50%;
  right: 0;
}
.program h2::before {
  position: absolute;
  top: 50%;
}
.program__btn {
  margin-bottom: 20px;
}
.program__wrap {
  background-color: #1E4D51;
  padding-top: 14px;
  padding-bottom: 14px;
}
.program__time, .program__text, .program__subtitle, .program__address {
  color: #e4b0a2;
}
.program__time {
  font-size: 14px;
  line-height: 94.5%;
  font-weight: 400;
}
.program__text {
  max-width: 130px;
  width: 100%;
  font-family: Oswald;
  margin-top: 14px;
}
.program__txt {
  max-width: 206px;
  width: 100%;
  font-size: 12px;
  line-height: 94.5%;
  font-weight: 400;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.program__img {
  margin: 0 auto;
  padding-bottom: 4px;
}
.program__wrapper {
  padding: 14px 28px;
  border: solid 1px #e4b0a2;
  margin-right: 14px;
  margin-left: 14px;
}
.program__subtitle {
  text-align: center;
  font-size: 18px;
  line-height: 94.5%;
  font-weight: 400;
  font-family: Playfair Display;
  margin: 20px 0 32px;
}
.program__image {
  margin-bottom: 20px;
}
.program__address {
  font-size: 16px;
  line-height: 94.5%;
  font-weight: 300;
}

.person__title {
  font-size: 24px;
  line-height: 94.5%;
  font-weight: 400;
  font-family: Playfair Display;
  text-align: center;
  margin: 32px 0 32px;
}
.person__img {
  margin: 0 auto;
}
.person__text {
  font-size: 22px;
  line-height: 94.5%;
  font-weight: 400;
  font-family: Oswald;
  margin-right: 6px;
  margin-top: 20px;
}
.person__icon {
  width: 16px;
  height: 16px;
}
.person__wrap {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 20px;
}

.person__dressCode {
  background-color: #1E4D51;
}
.person__dressCode__title, .person__dressCode__text--top, .person__dressCode__text--bottom {
  color: #e4b0a2;
}
.person__dressCode__wrapper {
  border: solid 1px #e4b0a2;
}

.person__present__wrapper {
  border: solid 1px #1e4d51;
}
.person__present__text--right {
  font-size: 14px;
  line-height: 94.5%;
  font-weight: 400;
}

.person__dressCode,
.person__present {
  padding-top: 15px;
  padding-bottom: 15px;
}
.person__dressCode__title,
.person__present__title {
  font-size: 23px;
  line-height: 94.5%;
  font-weight: 400;
  font-family: Playfair Display;
  text-align: center;
  margin: 12px 0 20px;
}
.person__dressCode__text--top, .person__dressCode__text--bottom,
.person__present__text--top,
.person__present__text--bottom {
  font-size: 12px;
  line-height: 94.5%;
  font-weight: 400;
  font-family: Oswald;
}
.person__dressCode__content,
.person__present__content {
  display: flex;
  align-items: center;
}
.person__dressCode__wrap,
.person__present__wrap {
  display: flex;
  margin: 0 0 27px 4px;
}
.person__dressCode__wrap--right,
.person__present__wrap--right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}
.person__dressCode__text--bottom,
.person__present__text--bottom {
  text-align: right;
}

.speakers__title {
  font-size: 24px;
  line-height: 94.5%;
  font-weight: 400;
  font-family: Playfair Display;
  text-align: center;
  margin-top: 49px;
}
.speakers__text {
  font-size: 22px;
  line-height: 94.5%;
  font-weight: 400;
  font-family: "Oswald-Regular", sans-serif;
  margin-right: 6px;
  margin-top: 20px;
}
.speakers__icon {
  width: 16px;
  height: 16px;
}
.speakers__wrap {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 20px;
}
.speakers__word {
  font-size: 20px;
  line-height: 94.5%;
  font-weight: 400;
  text-align: center;
  margin-top: 20px;
  font-family: "Oswald-Regular", sans-serif;
}
.speakers__img {
  margin: 0 auto;
  padding-top: 32px;
}
.speakers__btn {
  width: 146px;
  height: 40px;
  color: #E4B0A2;
  background: #1E4D51;
  font-size: 22px;
  line-height: 111.5%;
  font-weight: 400;
  box-shadow: 4px 4px 4px rgba(228, 176, 162, 0.25);
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  margin: 32px 0 95px;
}

.speakerCards__btn {
  margin-bottom: 30px;
  margin-top: 30px;
}
.speakerCards__wrap {
  background-color: #1E4D51;
  padding-top: 30px;
  padding-bottom: 30px;
}
.speakerCards__text, .speakerCards__subtitle {
  color: #e4b0a2;
  font-family: "Oswald-Regular", sans-serif;
}
.speakerCards__text {
  max-width: 264px;
  width: 100%;
  margin-top: 14px;
  text-align: center;
  font-size: 12px;
  line-height: 94.5%;
  font-weight: 400;
  padding-bottom: 6px;
}
.speakerCards__img {
  margin: 0 auto;
  border: solid 6px #e4b0a2;
  border-radius: 50%;
}
.speakerCards__wrapper {
  padding: 20px 21px;
  border: solid 1px #e4b0a2;
  margin-right: 14px;
  margin-left: 14px;
}
.speakerCards__subtitle {
  text-align: center;
  font-size: 22px;
  line-height: 94.5%;
  font-weight: 400;
}
.speakerCards__name {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin: 20px 0 20px;
}
.speakerCards__icon {
  margin-left: 6px;
}

